.modal__address {
    padding: 28px 30px;
    text-align: center;
}

.location-map {
    display: block;
    width: 100%;
    height: 199px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (min-width: 320px) {
    .location-map {
        width: 224px;
    }
}

@media screen and (min-width: 768px) {
    .modal__address {
        padding: 32px 60px;
    }

    .location-map {
        width: 448px;
        height: 260px;
        margin-top: 32px;
    }
}